.MovieDetails {
  display: flex;
  margin-top: 20px;
}

.Button {
  margin-left: auto;
  margin-right: auto;
  padding: 8px 8px;
  border-radius: 2px;
  background-color: #2e3030;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  text-align: center;
  display: block;
  color: #fff;
  border: 0;
  text-decoration: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 24px;
  font-style: normal;
  font-weight: 500;
  min-width: 100px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.Button:hover,
.Button:focus {
  background-color: #000000;
}

.Information {
  max-width: 500px;
  margin-left: 20px;
}

.Information_title {
  font-size: 20px;
  margin-top: 15px;
}

.Link {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 500;
  color: #2a363b;
  font-size: 18px;
}
